@mixin hearder-phone {
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 2px;
  color: #fff;
}

@mixin header-font-style {
  font-size: 12px;
  line-height: 16px;
  text-decoration: none;
  text-transform: uppercase;
  color: #fff;
  letter-spacing: 2px;
}

@mixin titles-font-style {
  font-weight: 700;
  font-size: 48px;
  line-height: 48px;
}

@mixin text-heading {
  margin-top: 0;
  font-weight: 700;
  font-size: 24px;
  line-height: 30px;
  color: #0075ff;
}
